<template>
  <div class="bookrack">
    <van-checkbox-group v-if="HasBookrackData" v-model="result" ref="checkboxGroup">
      <van-nav-bar
        title="追漫"
        :right-text="isShowSelectBox ? '取消编辑' : '编辑'"
        @click-right="onCompile"
      />
      <div class="content-box">
        <div
          class="rack-item"
          v-for="(bookrackItem,bookrackIndex) in bookrackData"
          :key="bookrackIndex"
          @click="isShowSelectBox ? '' : goIndetail(bookrackItem.id)"
        >
          <van-checkbox :name="bookrackItem.id" v-show="isShowSelectBox"></van-checkbox>
          <div class="img-box">
            <img v-lazy="bookrackItem.vertical_cover" />
          </div>
          <div class="title-box">
            <div class="title">{{bookrackItem.title}}</div>
            <div v-if="bookrackItem.is_finish === 0" class="text">更新至{{bookrackItem.total}}话</div>
            <div v-else class="text">[完结] 共{{bookrackItem.total}}</div>
          </div>
        </div>
      </div>
    </van-checkbox-group>
    <div v-else class="HasBookrackData">
      <div>
        <van-nav-bar title="追漫" left-arrow />
      </div>
      <div class="img-box">
        <img src="../assets/bea10bfe88465.png" alt />
        <div>什么都没有 ＞︿＜</div>
      </div>
    </div>

    <!-- 全选与反选 -->
    <div class="select-box" v-show="isShowSelectBox">
      <van-button type="info" @click="toggleAll">反选</van-button>
      <van-button class="remove" type="danger" @click="remove">删除</van-button>
      <van-button type="primary" @click="checkAll">全选</van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Bookrack",
  data() {
    return {
      // 收藏漫画列表数据
      bookrackData: [],
      isShowSelectBox: false,
      // 选择
      result: [],
      // 是否存在收藏漫画列表
      HasBookrackData: false
    };
  },
  created() {
    let storage = localStorage;
    if (storage.BookrackData === undefined) {
      localStorage.setItem("BookrackData", "[]");
      this.HasBookrackData = false;
    } else {
      let arr = JSON.parse(localStorage.getItem("BookrackData"));
      this.$root.bookData = Array.from(new Set(arr));
      this.splitArr(arr);
      //  是否有数据存在
      if (arr.length == 0) {
        this.HasBookrackData = false;
      } else {
        this.HasBookrackData = true;
      }
    }
  },
  methods: {
    // 全选反选
    checkAll() {
      this.$refs.checkboxGroup.toggleAll(true);
      
    },
    toggleAll() {
      this.$refs.checkboxGroup.toggleAll();
      
    },
    //  删除
    remove() {
      for (let i = 0; i < this.result.length; i++) {
        this.$root.bookData.splice(
          this.$root.bookData.indexOf(this.result[i]),
          1
        );
      }
      localStorage.setItem("BookrackData", JSON.stringify(this.$root.bookData));
      this.$router.go(0);
    },

    // 编辑
    onCompile() {
      this.isShowSelectBox = !this.isShowSelectBox;
    },
    //  拆分收藏漫画数据
    splitArr(arr) {
      // 
      for (let i = 0; i < arr.length; i++) {
        this.getBookrack(arr[i]);
      }
    },
    //  获取收藏漫画
    getBookrack(id) {
      // 
      this.axios({
        method: "get",
        url: "/ComicDetail",
        params: {
          comicId: id
        }
      })
        .then(res => {
          //  
          this.bookrackData.push(res.data.data);
          //  
        })
        .catch(err => {
          
        });
    },
    //  跳转漫画详情
    goIndetail(id) {
      this.$router.push({
        name: "Indetail",
        params: {
          id
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.bookrack {
  .content-box {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    padding: 0px 10px;
    position: fixed;
    left: 0;
    right: 0;
    top: 46px;
    margin: auto;
    box-sizing: border-box;
    overflow-y: scroll;
    .rack-item {
      width: calc(100% / 3 - 4px);
      margin-right: 6px;
      border-radius: 5px;
      box-sizing: border-box;
      margin-bottom: 15px;
      text-align: left;
      overflow: hidden;
      position: relative;
      &:nth-child(3n) {
        margin-right: 0px;
      }
      .van-checkbox {
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        align-items: flex-start;
        justify-content: flex-end;
      }
      img {
        width: 100%;
      }
      .title-box {
        width: 100%;
        .title {
          width: 100%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .text {
          color: #999;
        }
      }
    }
  }
  .select-box {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 50px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    padding: 0px 10px;
  }

  .HasBookrackData {
    .img-box {
      width: 100%;
      img {
        width: 100%;
      }
      font-size: 18px;
      text-align: center;
      color: #b0b7c0;
    }
    ::v-deep .van-nav-bar .van-icon {
      color: #b0b7c0;
    }
  }
}
</style>